.group {
    border: solid 1px #e3e3e3;
    margin-bottom: 20px;
    padding: 20px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: box-shadow .2s linear;
}

.group:hover {
    box-shadow: 5px 5px 5px #eee7e7;
}

.img{
    border-radius: 50%;
    margin-right: 20px;
    height: 70px;
    width: 70px;
}
.info {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


